$b: #233237;
$b2: #135b9c;
$bhover: #1e2c31;
$g: #797c81;

#titlebar {
  background: $b2;
  // background: linear-gradient(141deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
  height: 60px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  // box-shadow: 0 1px 1px 1px rgba(0,0,0,0.2);

  .title {
    text-align: center;
    font-size: 24px;
    color: white;
  }

  .info {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    font-size: 12px;
  }

  .apply {
    display: none;
  }

}


@media (min-width: 760px) {
  #titlebar {
    .apply {
      display: flex;
    }
  }
}



